import Vue from '../static/js/vue';
import axios from '../static/js/axios';
import wangEditor from '../static/js/wangEditor';
import store from './store';
import router from './router';
import App from './App';
Vue.prototype.axios = axios;
Vue.prototype.Editor = wangEditor;
Vue.config.productionTip = false;

new Vue({
  el: '#app',
  router:router,
  store:store,
  components: { App },
  template: '<App/>',
});



