<template>
  <div id="third-edit-content" :style="{height:main_height-122+'px'}">
    <div id="edit-menu"></div>
    <div id="edit-content" :style="{height:main_height-152+'px'}"></div>
  </div>
</template>

<script>
  export default {
    name: 'Editor',
    props:["main_height"],
    mounted:function () {
      /*
      * 创建富文本编辑器
      * */
      const E1 = this.Editor;
      const editor = new E1('#edit-menu','#edit-content');
      this.$store.state.editor = editor;
      editor.create();
    }
  }
</script>

<style scoped>

  #third-edit-content{
    width: 100%;
    z-index: 0!important;
  }
  #edit-menu{
    z-index: 1;
    width: 100%;
  }
  #edit-content{
    width: 100%;
    background: #ffffff;
    z-index: 1;
  }
</style>



