import Vue from '../../static/js/vue'
import Router from '../../static/js/vue-router'
import Login from '../components/Login'
import NoteBookModule from '../components/NoteBook'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/notebook',
      name: 'NoteBookModule',
      component: NoteBookModule
    },
    {
      path: '/',
      redirect: '/login'
    }

  ]
})



